.container{ 
    width: 60vw;
    margin-left: auto;
    margin-right: auto;
}
.reference{
    width: 100px;
    margin-top: -40vh;
    height: 45vh;
}
.controls{
    display: flex;
    flex-direction: column;
    align-items: right;
    justify-content: right;
    width: 60vw;
}
.projecttitle{
    font-size: 20px;
    margin-bottom: 32px;
}
.content{
    width: 60vw;
    display: flex;
    flex-direction: column;
    padding-top: 128px;
}
.links{
    text-decoration: none;
}
.returnbutton{
    color: #323232;
    background-color:#faf3e1;
    font-family: 'Courier New';
    font-size: 12pt;
    border: none;
    margin-top: 64px;
    padding: 16px;
    border-radius: 4px;
    max-width: 200px;
    text-align: left;
}
.returnbutton:hover{
    background-color: #56E39F;
    color: #323232;
    border: none;
    border-radius: 4px;
}