.container{
    margin-top: 64px;
}
.card{
    background-color:#faf3e1;
    padding: 16px;
    border-radius: 4px;
    width: 30vw;
    margin-top: 32px;
    z-index: 4;
    position: relative;
}
@media only screen and (max-width: 550px) {
    .card{
        background-color:#faf3e1;
        padding: 16px;
        border-radius: 4px;
        width: 60vw;
        margin-top: 32px;
        z-index: 4;
        position: relative;
    }
}
.title{
    font-family: "merriweather";
    font-weight: 600;
    font-size: 24px;
    margin-bottom: 32px;
}
.copy{
    font-size: 16px;
}
.image{
    width: 60vw;
    z-index: 3;

}
