body{
    margin: 0;
    background-color: #fffff1;
    color: #323232;
    font-family: 'Courier New';
    font-weight: 400;
}
.app{
    background-color: #fffff1;
}

.hero{
    background-color: #fffff1;
    color: #323232;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    padding-top: 16px;
    z-index: 1001;
    position: relative;
}
.herotext{
    font-size: 16pt;
}
.logo{
    width: 30vw;
    margin-top: 10vh;

}
.arrow{
    height: 5vh;
    margin-top: 124px;
}
.header{
    width: 100vw;
    height: 8vh;
    background-color: #fffff1;
    color: #323232;
    position: fixed;
    display: flex;
    flex-direction: row;
    justify-content: center;
    top:0px;
    left:0px;
    z-index:1000;
    padding-bottom: 16px;
}
.headername{
    margin: 32px
}
.links{
    margin: 32px
}
.section{
    background-color: #fffff1;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 100vh;
    width: 33vw;
    margin-left: auto;
    margin-right: auto;

}
.skills{
    background-color: #fffff1;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 65vw;
    margin-left: auto;
    margin-right: auto;

}

.intro{
    background-color: #fffff1;
    color: #323232;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 60vh;
    padding-left: 15vw;
    padding-right: 15vw;
    font-size: 14pt;
    font-weight: 900;
}
.footer{
    background-color: #323232;
    color: #fffff1;
}
.footercontent {
    display: flex;
    justify-content: end;
    flex-direction: column;
    align-items: center;
    text-align: center;
    height: 10vh;
    width: 60vw;
    padding: 32px;
    margin-left: auto;
    margin-right: auto;
}
.name{
    width: 48vw;
    margin-bottom: -24vw;
    z-index: 0;
}