

.title{
    font-size: 48px;
    font-family: 'Merriweather';
    font-weight: 600;
    text-align: left;
    margin-bottom: 32px;
}

@media only screen and (max-width: 550px) {
    .header{
        color: #323232;
        width: 60vw;
    }
.title{
    font-size: 24px;
    text-align: left;
    margin-bottom: 32px;
}
.info{
    width: 60vw;
    margin-bottom: 32px;
}
.subtitle{
    font-size: 16px;
    text-align: Left;
}
}

@media only screen and (min-width: 551px) {
    .header{
        color: #323232;
        width: 40vw;
    }
    .title{
        font-size: 32px;
        text-align: left;
        margin-bottom: 32px;
    }
    .info{
        width: 25vw;
    }
    .subtitle{
        font-size: 24px;
        text-align: left;

    }
    }




