@media only screen and (min-width: 551px) {
.class {
    font-family: 'Courier New';
    font-weight: 400;
    font-size: 16pt;
    color: #323232;
    border-radius: 4px;
    border-style: none;
    min-width: 300px;
    height: 50px;
    margin-bottom: 8px;
    margin-right: 8px;
    padding-left: 16px;
    display: flex;
    justify-content: left;
    align-items: center;
    background-color: #fffff1;
}
}

@media only screen and (max-width: 550px) {
    .class {
        font-family: 'Courier New';
        font-weight: 400;
        font-size: 16pt;
        color: #323232;
        border-radius: 4px;
        border-style: none;
        width: 65vw;
        height: 50px;
        margin-bottom: 8px;
        margin-right: 8px;
        padding-left: 16px;
        display: flex;
        justify-content: left;
        align-items: center;
        background-color: #fffff1;
    }
    }

.class:hover {
    background-color: #56E39F;
    color: #323232;
    border-radius: 4px;
    cursor: pointer;
}