.card{
background-color: #faf3e1;
color: #323232;
padding: 8px;
padding-left: 12px;
padding-right: 12px;
margin-bottom: 8px;
margin-right: 8px;
border-radius: 4px;
}
.copy{
font-size: 12pt;
font-weight: 100;
}

@media only screen and (max-width: 550px) {
    .card{
        background-color: #faf3e1;
        color: #323232;
        padding: 8px;
        padding-left: 10px;
        padding-right: 10px;
        margin-bottom: 8px;
        margin-right: 8px;
        border-radius: 4px;
        }
        .copy{
        font-size: 10pt;
        font-weight: 100;
        }
}