.contentbox{
    width: 40vw;
    display: flex;
    flex-direction: column;
    align-items: right;
    justify-content: right;
}
.image{
    width: 60vw;
    margin-top: 32px;
}
.description{
    width: 40vw;
    font-size: 18px;
}
.skillcontainer{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    width: 40vw;
    margin-top: 32px;
    margin-bottom: 32px;
}

@media only screen and (max-width: 550px) {
    .description{
        width: 60vw;
        font-size: 16px;
    }
    }
